@mixin reset {
    margin: 0;
    padding: 0;
}

@mixin center-block($width: 1040px) {
    width: 100%;
    max-width: $width;
    margin: 0 auto;
}

body {
    @include reset;
    width: 100%;
    font-family: "Montserrat", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    color: #533738;
    background-color: #efe6e4;
}

.header-wrapper {
    background-image: url(img/header-bg-img.jpg);
    background-repeat: no-repeat;
    background-position: center;
    padding: 35px 0 170px;
}

.header {
    @include center-block(1200px);
    display: flex;
    align-items: center;

    // .header__logo

    &__logo {
        margin-right: auto;
    }

    // .header__menu

    &__menu {
    }

    // .header__phone

    &__phone {
        margin-left: 10px;
        font-size: 16px;
        line-height: 20px;
    }
}
.logo {
    display: flex;
    align-items: center;

    // .logo__img

    &__img {
    }

    // .logo__text

    &__text {
        font-size: 16px;
        line-height: 20px;
    }
}
.menu {
    @include reset;
    display: flex;
    list-style-type: none;

    // .menu__item

    &__item {
        margin-right: 25px;

        &:hover .menu__link {
            color: #cc8b86;
        }
    }

    // .menu__link

    &__link {
        font-size: 16px;
        line-height: 20px;
        color: #533738;
        text-decoration: none;
    }
}
.submitting {
    @include center-block();
    margin-top: 90px;

    // .submitting__slogan

    &__slogan {
        font-size: 50px;
        line-height: 62px;
    }

    // .submitting__text

    &__text {
        margin: 25px 0 20px;
    }

    // .submitting__btn

    &__btn {
    }
}
.btn {
    padding: 25px 65px;
    font-size: 20px;
    line-height: 24px;
    color: #533738;
    background-color: #cc8b86;
    outline: none;
    border: none;
    cursor: pointer;
}
